import styled, { css } from "styled-components";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { Title2, Text, InnerWrap } from "../../assets/styles/component";

import joystick from "../../assets/images/Joystick.png";
import ecom_item_1 from "../../assets/images/box.svg";
import ecom_item_2 from "../../assets/images/ticket.svg";
import ecom_item_3 from "../../assets/images/joystick.svg";
import violet_arrow from "../../assets/images/violet_arrow.svg";
import yellow_arrow from "../../assets/images/yellow_arrow.svg";

const SECommerce = styled.section`
  position: relative;
  padding: 200px 0;
  padding-bottom: 300px;

  background: radial-gradient(
      25.11% 34.26% at 75.11% 83.62%,
      rgba(241, 2, 117, 0.37) 0%,
      rgba(241, 2, 117, 0) 100%
    ),
    radial-gradient(
      47.96% 65.44% at 93.27% 109.7%,
      rgba(255, 66, 158, 0.32) 0%,
      rgba(255, 66, 158, 0) 100%
    ),
    radial-gradient(
      38.06% 38.06% at 55.9% 65.19%,
      rgba(2, 2, 241, 0.3) 0%,
      rgba(2, 2, 241, 0) 100%
    );

  & > div {
    & * {
      z-index: 5;
    }

    & > p {
      text-align: center;
      margin-bottom: 170px;
    }
  }

  @media (max-width: 1300px) {
    & > div > p {
      margin-bottom: 130px;
    }
  }

  @media (max-width: 1100px) {
    padding: 70px 0;
    padding-bottom: 170px;

    & > div > p {
      margin-bottom: 130px;
    }
  }
`;

const Title = styled(Title2)`
  margin-bottom: 40px;

  @media (max-width: 1100px) {
    margin-bottom: 20px;
  }
`;

const Joystick = styled(LazyLoadImage)`
  position: absolute;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%) rotate(-5deg);
  z-index: 3;
  width: 78vw;
  min-width: 1000px;
  max-width: 1250px;

  animation: move 10s ease-in-out infinite;

  @keyframes move {
    0% {
      transform: translateX(-50%) rotate(-5deg);
    }

    50% {
      transform: translateX(-50%) translateY(1%) rotate(-5deg);
    }

    100% {
      transform: translateX(-50%) rotate(-5deg);
    }
  }

  @media (max-width: 1300px) {
    bottom: calc(10% + 100px);
    min-width: 600px;
    width: 60vw;
  }

  @media (max-width: 1100px) {
    bottom: 100px;
  }
`;

const List = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 313px 313px;
  width: 100%;
  gap: 117px 0;
  z-index: 5;

  justify-content: center;

  @media (max-width: 1300px) {
    grid-template-rows: 222px 222px;
    gap: 0;
  }
`;

const ListItem = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  justify-self: start;
  z-index: 5;

  &:not(:last-child) {
    max-width: 405px;
    padding: 90px 55px 52px 54px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.19);
    backdrop-filter: blur(25px);
    border-radius: 46px;

    @media (max-width: 1300px) {
      padding: 54px 23px 25px 23px;
      height: fit-content;
      border-radius: 20px;
      max-width: 245px;

      p {
        font-size: 10px;
        line-height: 20px;
      }
    }
  }

  &:nth-child(1) {
    img:first-child {
      left: -30px;
    }

    img:last-child {
      bottom: -30%;
      max-width: 210px;
      width: 50%;
      left: 15%;
    }
  }

  &:nth-child(3) {
    img:first-child {
      left: -10px;
    }

    img:last-child {
      left: -36%;
      top: -10%;
      max-width: 132px;
    }
  }

  &:nth-child(4) {
    img:first-child {
      right: 10px;
    }
  }

  &:nth-child(3n) {
    justify-self: end;
  }

  &:last-child {
    flex-direction: column;
    gap: 53px;
    width: 100%;
    max-width: 450px;

    @media (max-width: 1200px) {
      gap: 33px;
      max-width: 245px;
      margin-right: 7%;

      p {
        font-size: clamp(14px, 1vw, 20px);
        line-height: 140%;
      }
    }
  }
`;

const Promo = styled.div``;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: 26px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 33px;
  width: 90%;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }

  @media (max-width: 1200px) {
    max-width: 200px;
    padding: 17px 34px;
    outline: 5px solid rgba(255, 255, 255, 0.1);
    font-size: 14px;
  }
`;

const Img = styled(LazyLoadImage)`
  position: absolute;
  width: 180px;
  height: 180px;
  top: -70px;

  @media (max-width: 1300px) {
    top: -90px;
    width: 128px;
  }
`;

const Arrow = styled(LazyLoadImage)`
  position: absolute;
`;

const ECommerce = ({ language }) => {
  return (
    <SECommerce>
      <InnerWrap>
        <Title>
          Эффективно <br /> для e-commerce
        </Title>
        <Text size="20">
          Вместо обычных ссылок вы сможете интегрировать <br /> в неограниченное
          количество стримов уникальный промо-формат
        </Text>

        <List>
          <ListItem>
            <Img src={ecom_item_1} alt="" loading="lazy" />
            <Text>
              Доставка товаров занимает время + дополнительные издержки на
              логистику, косты могут быть не оправданы
            </Text>
            <Arrow src={violet_arrow} alt="" loading="lazy" />
          </ListItem>

          <div />

          <ListItem>
            <Img src={ecom_item_3} alt="" loading="lazy" />
            <Text>
              Не всем товарам нужна демонстрация: иногда достаточно промо-кода,
              либо интерактивного видео, встроенного в стрим
            </Text>
            <Arrow src={yellow_arrow} alt="" loading="lazy" />
          </ListItem>

          <ListItem>
            <Img src={ecom_item_2} alt="" loading="lazy" />
            <Text>
              Как следствие: не все товары можно показать физически, <br /> не
              все товары удобно интегрировать в стримы
            </Text>
          </ListItem>

          <Promo></Promo>

          <ListItem>
            <Text size="26">
              Узнайте, как SocialStream помогает в продажах товаров
              интернет-магазинов
            </Text>
            <Button>Начать общение</Button>
          </ListItem>
        </List>
      </InnerWrap>

      <Joystick src={joystick} alt="Joystick" loading="lazy" />
    </SECommerce>
  );
};

export default ECommerce;
